body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-row{
  padding: 0%;
  text-align: center;
  align-items: center;
  background-color: black;
  background: rgba(81, 82, 85, 0.3);
  color: white;
}

.image-row{
  margin: 0%;
  padding: 0%;
}

.menu{
  padding: 1.5rem;
}

.menu-right{
  text-align: right !important;
  
  margin-left: auto;
}

.menu-left{
  text-align: left !important;
}


.menu:hover{
  background-color: lightblue;
}

.menu-text{
  color: white !important; 
  text-decoration: none !important;
  font-size: 1.1em;
}

.space-center{
  text-align: center;
}

.search-input > .bx--search-input{
  height: 80% !important;
  font-size: 1.5rem !important;
  background-color: white !important;
  border-style: solid;
  border-radius: 3rem;
}

.search-input > .bx--search-magnifier{
  height: 24px;
  width: 24px;
  top: 1.3rem;
  fill:black;
}

.bx--tile-custom {
  height: 4rem;
}

.search-input{
  height: 100% !important;
  align-items: center;
}


.slider-custom{
 margin-top: 2rem;
 padding: 0%;
}

.tile-popular{
  margin: 4rem 8rem;
  padding: 2rem 2rem 4rem 3rem;
}

.slick-prev {
  left:0% !important;
  z-index: 1;
  width: 40px !important;
  height: 40px !important;
  background: lightblue !important;
  border-radius: 50%;
}

.slick-prev:before, .slick-next:before{
  font-size: 34px !important;
}

.slick-next {
  right: 0% !important;
  z-index: 1;
  width: 40px !important;
  height: 40px !important;
  background: lightblue !important;
  border-radius: 50%;
}
.rating-custom{
  font-size: 28px;
  padding-top: 4%;
  align-items: center;
}
.footer-custom{
  margin-top: 4rem;
}
.search-input > .bx--search-input{
  height: 80% !important;
  font-size: 1.5rem !important;
  background-color: white !important;
  border-style: none;
  border-radius: 3rem;
}

.search-input > .bx--search-magnifier{
  height: 24px;
  width: 24px;
  top: 0.5rem;
  fill:black;
}

.bx--tile-custom {
  width: 100%;
  height: 4rem;
   
}

.search-input{
  height: 100% !important;
  top:35%;
  align-items: center;
  display: flex;
}

.custom-combobox{
  top:35%;
  background-color: white;
}

.bx--text-input{
  box-shadow: 0 0 0 0 white!important;
}
.rating-custom{
  font-size: 40px;
  padding-top: 4%;
  align-items: center;
}
.header-style {
  align-items: center;
  background-color: white;
  color: black;
  height: 6rem;
  text-align: center;
  margin: 0px 0px; }
  .header-style img {
    max-width: 35px;
    height: auto; }
  .header-style a {
    color: black; }

.header-menu {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }
  .header-menu a:link, .header-menu a:visited {
    text-decoration: none;
    font-size: 16px;
    color: black; }
  .header-menu.header-border {
    border-left: solid #e2e1e1 1px;
    border-right: solid #e2e1e1 1px; }

